html {
  font-family: sans-serif;
}

button, #btn {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #68cd09;
  border: none;
  border-radius: 3rem;
  justify-self: flex-start;
  max-width: 20rem;
  max-height: 6rem;
  padding: 5px 18px;
  font-size: 3rem;
  top: 50vh;
}

form {
  flex-direction: column;
  display: flex;
}

form div label {
  text-align: right;
  color: #04044b;
  width: 9rem;
  display: inline-block;
}

form div input {
  border: 2px solid #ef5d52;
  border-radius: 2rem;
  outline-color: #cdcd0d;
  width: 30%;
  height: 2rem;
  margin-block-end: 1rem;
  padding: 1rem;
}

